<script>
import PhotoSwipe from "photoswipe/lightbox";
import 'photoswipe/style.css';

export default {
    name: "ImageViewer",

    props: [
        'isOpen',
        'images',
        'onClose'
    ],

    data() {
        return {
            imagesData: [],
        }
    },

    async mounted() {
        if (!this.lightbox) {

            this.imagesData = await Promise.all(this.images.map(async (src) => {
                return new Promise((resolve) => {
                    const img = new Image();
                    img.onload = () => resolve({ src, w: img.naturalWidth, h: img.naturalHeight });
                    img.src = src;
                });
            }));

            const options = {
                gallery: '#gallery',
                children: 'a',
                pswpModule: () => import('photoswipe'),
                loop: false,
                zoom: false,
                close: false,
                secondaryZoomLevel: 2,
                maxZoomLevel: 3,
                counter: false,
                spacing: 0.05
            };
            this.lightbox = new PhotoSwipe(options);

            this.lightbox.on('uiRegister', () => {
                this.lightbox.pswp.ui.registerElement({
                    name: 'bulletsIndicator',
                    className: 'pswp__bullets-indicator',
                    appendTo: 'wrapper',
                    onInit: (element, pswp) => {
                        const bullets = [];
                        let bullet;
                        let prevIndex = -1;

                        for (let i = 0; i < pswp.getNumItems(); i++) {
                            bullet = document.createElement('div');
                            bullet.className = 'pswp__bullet';
                            bullet.onclick = (e) => {
                                pswp.goTo(bullets.indexOf(e.target));
                            };
                            element.appendChild(bullet);
                            bullets.push(bullet);
                        }

                        pswp.on('change', () => {
                            if (prevIndex >= 0) {
                                bullets[prevIndex].classList.remove('pswp__bullet--active');
                            }
                            bullets[pswp.currIndex].classList.add('pswp__bullet--active');
                            prevIndex = pswp.currIndex;
                        });
                    }
                })
            })

            this.lightbox.init();
        }
    },

    unmounted() {
        if (this.lightbox) {
            this.lightbox.destroy();
            this.lightbox = null;
        }
    },
}
</script>

<template>
    <div id="gallery">
        <a v-for="(imageData) in imagesData"
           :key="imageData.src"
           :href="imageData.src"
           :data-pswp-srcset="imageData.src"
           :data-pswp-width="imageData.w"
           :data-pswp-height="imageData.h"
           target="_blank"
           rel="noreferrer">
            <button>open</button>
        </a>
    </div>
</template>

<style src="./image-viewer.css"/>
